"use client";
import React, { useEffect, useState } from "react";
import Highcharts from "highcharts";
import HighchartsReact from "highcharts-react-official";
import Highcharts3D from "highcharts/highcharts-3d";
import axios from "axios";

Highcharts3D(Highcharts);

export default function AnalyticsPage() {
  const [overview, setOverview] = useState([]);
  const [monthly, setMonthly] = useState([]);

  useEffect(() => {
    axios
      .get("{{matrimonial}}/admin/overview")
      .then((res) => setOverview(res.data.data))
      .catch((err) => console.log(err));

    axios
      .get("{{matrimonial}}/admin/matches-per-month")
      .then((res) => setMonthly(res.data.data))
      .catch((err) => console.log(err));
  }, []);

  const colors = {
    Completed: "#0a8a1f",
    Moderate: "#f3d034",
    Low: "#f79c1c",
    Incomplete: "#d91616",
  };

  return (
    <div
      style={{
        display: "grid",
        gridTemplateColumns: "1fr 1fr",
        gap: 30,
        padding: 20,
      }}
    >
      {/* --------------------------------------- */}
      {/*            PROFILE OVERVIEW             */}
      {/* --------------------------------------- */}
      <div
        style={{
          background: "#fff",
          borderRadius: 18,
          padding: 25,
          border: "1px solid #ddd",
          boxShadow: "0 3px 10px rgba(0,0,0,0.10)",
        }}
      >
        {/* HEADER + LEGEND */}
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            paddingRight: 10,
            alignItems: "center",
          }}
        >
          <h2 style={{ fontSize: 26, fontWeight: 600 }}>Profile Overview</h2>

          {/* LEGEND */}
          <div style={{ display: "flex", gap: 22 }}>
            {overview.map((item, idx) => (
              <div
                key={idx}
                style={{
                  display: "flex",
                  alignItems: "center",
                  gap: 8,
                  fontSize: 17,
                  fontWeight: 500,
                }}
              >
                <div
                  style={{
                    width: 16,
                    height: 16,
                    borderRadius: 4,
                    border: `2.5px solid ${colors[item.name]}`,
                  }}
                ></div>
                {item.name}
              </div>
            ))}
          </div>
        </div>

        {/* PIE CHART */}
        <HighchartsReact
          highcharts={Highcharts}
          options={{
            chart: {
              type: "pie",
              backgroundColor: "transparent",
              options3d: { enabled: true, alpha: 45 },
              height: 350,
            },
            credits: { enabled: false },
            tooltip: { pointFormat: "<b>{point.percentage:.1f}%</b>" },

            plotOptions: {
              pie: {
                depth: 45,
                dataLabels: { enabled: false },
              },
            },

            series: [
              {
                name: "Profiles",
                data: overview.map((item) => ({
                  name: item.name,
                  y: item.value,
                  color: colors[item.name],
                })),
              },
            ],
          }}
        />
      </div>

      {/* --------------------------------------- */}
      {/*           MATCHES PER MONTH             */}
      {/* --------------------------------------- */}
      <div
        style={{
          background: "#fff",
          borderRadius: 18,
          padding: 25,
          border: "1px solid #ddd",
          boxShadow: "0 3px 10px rgba(0,0,0,0.10)",
        }}
      >
        {/* HEADER + LEGEND SAME LINE */}
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            paddingRight: 10,
            alignItems: "center",
          }}
        >
          <h2 style={{ fontSize: 26, fontWeight: 600 }}>Matches Per Month</h2>

          <div style={{ display: "flex", gap: 30 }}>
            {/* USERS LEGEND */}
            <div style={{ display: "flex", alignItems: "center", gap: 8 }}>
              <div
                style={{
                  width: 16,
                  height: 16,
                  borderRadius: 4,
                  border: "2.5px solid #eec62b",
                }}
              ></div>
              <span style={{ fontSize: 17, fontWeight: 500 }}>
                Total no. of Users
              </span>
            </div>

            {/* MATCHES LEGEND */}
            <div style={{ display: "flex", alignItems: "center", gap: 8 }}>
              <div
                style={{
                  width: 16,
                  height: 16,
                  borderRadius: 4,
                  border: "2.5px solid #e22525",
                }}
              ></div>
              <span style={{ fontSize: 17, fontWeight: 500 }}>
                No. Of Matches
              </span>
            </div>
          </div>
        </div>

        {/* CHART */}
        <HighchartsReact
          highcharts={Highcharts}
          options={{
            chart: {
              height: 380,
              backgroundColor: "transparent",
            },

            credits: { enabled: false },

            xAxis: {
              categories: monthly.map((d) => d.month),
              labels: { style: { fontSize: 14 } },
            },

            yAxis: {
              title: { text: "" },
              labels: {
                formatter() {
                  return this.value + " M";
                },
                style: { fontSize: 14 },
              },
            },

            tooltip: { shared: true },

            series: [
              {
                type: "column",
                name: "Total Users",
                data: monthly.map((d) => d.totalUsers),
                borderRadius: 6,
                color: {
                  linearGradient: { x1: 0, x2: 0, y1: 0, y2: 1 },
                  stops: [
                    [0, "#f4c542"],
                    [1, "#d79d1c"],
                  ],
                },
              },

              {
                type: "line",
                name: "Matches",
                data: monthly.map((d) => d.matches),
                color: "#e22525",
                lineWidth: 3,
                marker: {
                  enabled: true,
                  radius: 6,
                  fillColor: "#fff",
                  lineWidth: 3,
                  lineColor: "#e22525",
                },
              },
            ],
          }}
        />
      </div>
    </div>
  );
}
